* {
  margin: 0;
  padding: 0;
}

:root {
  --primary: #348ccc;
}

body {
  font-family: "Poppins", sans-serif;
}

.graph {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 0 20px 0;
}

.graph-cont {
  display: flex;
  justify-content: center;
  align-items: center;
}

.linegraph {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 30px;
  width: 60%;
  max-width: 700px;
  box-shadow: 0px 3px 18px 3px rgba(0, 0, 0, 0.1);
  border-radius: 20px;
  margin: 50px 0;
}

@media only screen and (max-width: 952px) {
  .linegraph {
    width: 70%;
    margin: 40px auto;
  }
}

@media only screen and (max-width: 667px) {
  .linegraph {
    width: 80%;
    margin: 40px 0;
  }
}
